.hotsale {
  padding: 10px;
}
.hotsale-category-img1 img,
.hotsale-category-img img {
  width: 100%;
  height: 100%;
}
.sale-imgs {
  display: flex;
  flex-direction: column;
}
.hotsale-category-img,
.hotsale-category-img1 {
  position: relative;
  padding: 5px;
}

.hotsale-category-text {
  position: absolute;
  top: 0;
  display: flex;
  align-items: left;
  justify-content: center;
  flex-direction: column;
  font-size: x-small;
  padding-top: 2rem;
}
.hotsale-category-text h6 {
  font-size: small;
}
.show-now {
  background-color: #ffb218;
  border: 2px solid #fff4e7;
  padding: 3px;
  border-radius: 5px;
  font-weight: 500;
  color: #fff;
}
.show-now:hover {
  background-color: antiquewhite;
  color: #000;
}
.hotsale-category-img:hover,
.hotsale-category-img1:hover {
  box-shadow: 2px 6px 25px rgba(198, 198, 198, 0.558);
}
.sale-col-img img {
  width: 100%;
  height: 100%;
}

.time-offer-days .span {
  width: 40px;
  height: 40px;
  background-color: #ffb218;
  border-radius: 50%;
  text-align: center;
  padding: 10px;
}
.time-offer-day-text {
  font-size: small;
}
.time-offer-days {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.time-left-sale {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.column-sales {
  border: 1px solid rgba(128, 128, 128, 0.284);
  padding: 20px;
  height: 100%;
}
.sale-flex {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.column-sales:hover {
  box-shadow: 0px 7px 20px rgba(128, 128, 128, 0.325);
}
