.satisfy-regular {
  font-family: "Satisfy", cursive;
  font-weight: 400;
  font-style: normal;
}
.m-plus1p-regular {
  font-family: "M PLUS 1p", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.kids-banner-img img {
  width: 100%;
  height: 100%;
}
.new-collection-kids {
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  color: #fff;
}
.new-collection-kids h1 {
  font-size: 3rem;
}
.kids-clothes-girls {
  background-color: pink;
  display: flex;
  align-items: center;
  justify-content: center;
}
.kids-clothes-boys {
  background-color: powderblue;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content-carousel-kids {
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-weight: 600;
}
.button-shopkids {
  border: none;
  border-radius: 20px;
  padding: 8px 25px;
  color: #363636;
  background-color: rgb(255, 216, 222);
}
.button-shopkids-boy {
  border: none;
  border-radius: 20px;
  padding: 8px 25px;
  color: #363636;
  background-color: rgb(204, 249, 255);
}
.img-col-kid img {
  width: 100%;
  height: 100%;
}
.img-col-kid:hover {
  transform: scale(1.1);
}
.img-col-kid {
  position: relative;
  transition: transform 0.5s ease-in-out;
}
.kids-section-2,
.kids-section-3 {
  padding: 2rem 0;
}
.col-kid-text {
  position: absolute;
  top: 0;
  padding: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.col-kid-text-heading {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
}

.col-kid-text-shop {
  position: relative;
  font-weight: 700;
  color: #fff;
}
.col-kid-text-shop a {
  text-decoration: none;
  color: #fff;
}
.col-kid-text-shop::before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
}
.editor-pick {
  color: rgb(57, 156, 169);
  text-align: center;
}
.product-blocks img {
  width: 100%; /* Ensure the image takes the full width of its container */
  height: auto; /* Maintain aspect ratio */
}
.product-blocks {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.product-heading {
  padding-top: 1rem;
  font-weight: 600;
  /* letter-spacing: 1.4px; */
  font-size: 16px;
}
.product-review i {
  margin-right: 5px;
}
.product-review {
  padding-bottom: 0.5rem;
  font-size: small;
}
.product-price {
  color: red;
  font-size: small;
}
.product-colors img {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 2px solid white;
}
.product-colors {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
}
.product-colors img:hover {
  border: 2px solid rgb(255, 162, 0);
  cursor: pointer;
  padding: 2px;
}
.img-icons {
  margin-bottom: -2rem;
  position: absolute;
  display: none;
}
.img-icons i {
  background: rgb(255, 173, 187);
  padding: 10px;
}
.product-blocks:hover {
  .img-icons {
    display: block;
    cursor: pointer;
  }
}
.product-detail-2 {
  padding-top: 2rem;
  padding-bottom: 3rem;
}
.product-shopall-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
/* .product-shopall-btn button {
  width: 100px;
} */
.product-shopall-btns {
  border: none;
  padding: 10px 30px;
  border-radius: 20px;
  background-color: rgba(85, 183, 196, 0.709);
  color: #fff;
  font-weight: 600;
}
.kids-section-4 {
  position: relative;
  color: #fff;
}
.kids-section-4 img {
  width: 100%;
  height: 45rem;
}
.kids-section-4 {
  position: relative;
}
.kids-section-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.clearance-sale-heading h2 {
  font-size: 4rem;
}
.countdown {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.clearance-sale-subheding h3 {
  margin-bottom: 3rem;
}
.days-hrs-min,
.days-hrs-min2,
.days-hrs-min3,
.days-hrs-min4 {
  background-color: #c8e4c3;
  padding: 20px;
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(24, 79, 0);
  font-weight: 600;
  font-size: 16px;
}
.clearance-sale-text p {
  padding: 20px;
  margin-top: 20px;
  font-size: 20px;
  line-height: 2;
  font-weight: 600;
}
.kids-section-content .product-shopall-btns {
  border: none;
  padding: 10px 35px;
  border-radius: 20px;
  background-color: #c8e4c3;
  color: rgb(24, 79, 0);
  font-weight: 600;
}
.kids-section-5 {
  padding: 2rem 0;
}
