.top-categories {
  padding: 10px;
  /* display: flex; */
  /* align-items: center; */
}
.product-card {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: rgba(245, 245, 245, 0.846);
  padding: 10px;
  font-size: medium;
  height: 100px;
}
.product-img img {
  width: 100px;
}
.product-card:hover {
  transform: scale(1.1);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
@media (min-width: 550px) and (max-width: 880px) {
  .product-img img {
    width: 70px;
    padding: 10px;
  }
  .product-card {
    font-size: x-small;
  }
}
