.carousel-caption {
  position: absolute;
  top: 10%;
  left: 0;
  text-align: left;
  padding: 100px;
}
.carousel-caption p {
  font-size: x-large;
  color: #fff;
}
.carousel-caption h5 {
  font-size: 3rem;
  font-weight: 700;
  color: #fff;
}
.banner-carousel-buttons {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 20px;
  font-size: medium;
}
.banner-carousel-buttons button {
  padding: 10px 15px;
  border: 2px solid #fff;
  /* background-color: antiquewhite; */
  font-weight: 600;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.view-categories {
  cursor: pointer;
  position: relative;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  transition: all 1s;
  background-color: aliceblue;
  &:after,
  &:before {
    content: " ";
    width: 20px;
    height: 20px;
    position: absolute;
    border: 0px solid #fff;
    transition: all 1s;
  }
  &:after {
    top: -1px;
    left: -1px;
    border-top: 2.5px solid rgba(255, 213, 0, 0.719);
    border-left: 2.5px solid rgba(255, 213, 0, 0.719);
  }
  &:before {
    bottom: -1px;
    right: -1px;
    border-bottom: 2.5px solid rgba(255, 213, 0, 0.719);
    border-right: 2.5px solid rgba(255, 213, 0, 0.719);
  }
  &:hover {
    border-top-right-radius: 0px;
    border-bottom-left-radius: 0px;
    /* // background:rgba(0,0,0,.5); */
    /* // color:white; */
    &:before,
    &:after {
      width: 100%;
      height: 100%;
      /* // border-color:white; */
    }
    background-color: #da9100;
    color: #fff;
  }
}

.view-collection {
  cursor: pointer;
  position: relative;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  transition: all 1s;
  background-color: transparent;
  color: #fff;
  &:after,
  &:before {
    content: " ";
    width: 20px;
    height: 20px;
    position: absolute;
    border: 0px solid #fff;
    transition: all 1s;
  }
  &:after {
    top: -1px;
    left: -1px;
    border-top: 2.5px solid rgba(255, 213, 0, 0.421);
    border-left: 2.5px solid rgba(255, 213, 0, 0.421);
  }
  &:before {
    bottom: -1px;
    right: -1px;
    border-bottom: 2.5px solid rgba(255, 213, 0, 0.421);
    border-right: 2.5px solid rgba(255, 213, 0, 0.421);
  }
  &:hover {
    border-top-right-radius: 0px;
    border-bottom-left-radius: 0px;
    /* // background:rgba(0,0,0,.5); */
    /* // color:white; */
    &:before,
    &:after {
      width: 100%;
      height: 100%;
      /* // border-color:white; */
    }
    background-color: aliceblue;
    color: #000;
  }
}
.offer-banner-img img {
  width: 100%;
  height: 100%;
}
