.banner-about {
  height: 10rem;
  width: 100%;
  background-color: rgba(250, 235, 215, 0.705);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-bg {
  background-color: rgba(250, 235, 215, 0.459);
  padding: 40px;
  width: 100%;
}
.about-us-content {
  text-align: center;
  padding: 2rem 5rem;
}
.about-img img {
  width: 100%;
  height: 100%;
}
.about-me-left h4 {
  padding-bottom: 1rem;
}
@media (max-width: 880px) {
  .about-me-left p {
    font-size: 12px;
  }
  .about-me-left h4 {
    padding-bottom: 0rem;
  }
}
